import React from 'react';
import { useNavigate } from '@remix-run/react';
import ExpenseForm from '~/components/expenses/ExpenseForm';
import Modal from '~/components/util/Modal';
import { addExpense } from '~/data/expenses.server';
import { redirect } from '@remix-run/node';
import { validateExpenseInput } from '~/data/validation.server';
import { requireUserSession } from '~/data/auth.server';

const ExpensesAddPage = () => {
  const navigate = useNavigate();
  const closeHandler = () => {
    navigate('..');
  };

  return (
    <Modal onClose={closeHandler}>
      <ExpenseForm />
    </Modal>
  );
};

export async function action(data) {
  console.log('🚀 ~ file: add.jsx:24 ~ action ~ data:', data);
  const userId = await requireUserSession(data.request);
  const formData = await data.request.formData();
  const expense = Object.fromEntries(formData);

  try {
    validateExpenseInput(expense);
  } catch (error) {
    return error;
  }

  await addExpense(expense, userId);
  return redirect('/expenses');
}

export default ExpensesAddPage;
